.MoneyEx {
  @apply h-screen container mx-auto w-11/12 lg:w-10/12 2xl:w-8/12;

  .gridWrap {
    @apply grid grid-cols-1 gap-3 sm:gap-6 2xl:gap-8;
    @apply mt-5 lg:mt-10;
  }

  .inputOutput {
    // @apply row-span-1 col-span-4;
    @apply bg-white rounded-lg shadow-xl;
    @apply flex flex-row items-center;
    @apply px-3 py-3 sm:px-16 sm:py-7 xl:px-20 xl:py-10 sm:space-x-1;
    @apply text-lg sm:text-2xl md:text-3xl xl:text-4xl font-semibold;

    .codeSymbolGroup{
      @apply w-24 sm:w-28 xl:w-32 pl-2;
      word-spacing: -10px;
      @apply text-lime-600 text-opacity-60;
      @apply font-mono;
    }

    .inputField {
      @apply outline-none w-full sm:w-5/6;
      @apply text-gray-600 font-bold tracking-wide;
      @apply font-mono;
      @apply placeholder:text-base sm:placeholder:text-2xl placeholder:text-gray-300;
    }

    .clearInputIcon {
      @apply w-4 h-4 sm:w-6 sm:h-6 opacity-50;
      @apply hover:opacity-80;
    }
  }

  .favList {
    @apply grid grid-cols-1 sm:grid-cols-2 xl:grid-cols-3;
    @apply gap-3 xl:gap-4 p-3 md:p-4;
    @apply border bg-gray-100 rounded-lg shadow-xl;
    
    .itemWrap {
      @apply py-1 border rounded-lg bg-white;
    }

    .favItemActionBtnGroup {
      @apply grid grid-cols-2;
      @apply mt-2 -mb-1;
    }
    
    .favItemActionBtn {
      font-size: 0.65rem;
      line-height: .75rem;
      @apply first:rounded-bl-lg border-r-2 last:border-r-0 last:rounded-br-lg p-1;
      @apply text-slate-400 tracking-widest;
      @apply bg-gradient-to-b from-slate-200;
      @apply hover:bg-gradient-to-t hover:from-slate-300;
    }

    .itemInfoWrap {
      @apply flex flex-row;
      
      &:hover {
        @apply cursor-pointer;
      }
      .favItemRightGroup {
        @apply flex flex-col justify-center;
      }
      .favItemSymbolValueGroup {
        word-spacing: -5px;
      }
      .name {
        @apply text-gray-400 text-xs sm:text-xs md:text-sm;
      }
      .favItemSymbol {
        @apply text-gray-400;
      }
      .exRate {
        @apply text-sm md:text-base font-bold text-gray-700 tracking-wide font-mono;
      }
      .codeFlagGroup {
        @apply h-12 w-24 rounded-xl border bg-gray-100 p-3 text-center scale-75;
        @apply flex flex-row justify-center;
        @apply text-base font-extrabold tracking-wide text-gray-600;
      }
    }
  }

  .fullList {
    @apply row-span-6 col-span-2;
    @apply grid grid-cols-1 md:grid-cols-4 lg:grid-cols-5 xl:grid-cols-7;
    @apply max-h-fit overflow-y-auto;
    @apply border bg-gray-100 rounded-lg shadow-xl;
    @apply text-xs;
  
    .itemWrap {
      @apply h-28 m-1 p-2;
      @apply border bg-white;
  
      &:hover {
        @apply bg-slate-500 cursor-pointer;
      }
      .codeFlagGroup {
        @apply p-1 rounded-full bg-gray-200;
      }
      .name {
        @apply mt-1 text-gray-700;
      }
    }
  }

  .starColor {
    filter: brightness(1) grayscale(1);
    // filter: invert(48%) sepia(79%) saturate(2476%) hue-rotate(86deg) brightness(118%) contrast(119%);
  }
}
